import UniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import UniBackground from '@/components/uni-background/uni-background.vue';
import FunButton from '@/components/fun-button.vue';
export default{
	components:{
		UniNavBar,
		UniBackground,
		FunButton
	},
	data(){
		return {
			scroll:0,
			navButtons:{
				back:{
					type:'normal',
					text:'取消'
				}
			},
			imageLib:{
				filter:'../../static/icons/filter.png',
				sold:'../../static/icons/icon_sold.png',
				bought:'../../static/icons/icon_bought.png',
				alipay:'../../static/icons/icon_alipay.png',
				wechatpay:'../../static/icons/icon_wechatpay.png',
				unionpay:'../../static/icons/icon_unionpay.png',
				add:'../../static/icons/icon_add.png',
			},
			activeTab:0,
			transList:[
				{
					sold:false,
				},
				{
					sold:true,
				}
			],
			isVip:true,
			showAd:true
		}
	},
	onPageScroll(val){
		this.scroll = val.scrollTop;
	},
	methods:{
		openVip(){
			if(this.isVip){
				uni.navigateTo({
					url:'../publishbill/publishbill'
				})
			}else{
				uni.showModal({
					title:'您当前没有发布挂单资格',
					content:'升级为SVIP用户后，即可享有挂单资格',
					confirmText:'去升级',
					success:res=>{
						console.log(res);
						if(res.confirm){
							uni.navigateTo({
								url:'../paysvip/paysvip'
							})
						}else if(res.cancel){
							
						}
					}
				})
			}
		}
	}
}
